import { createAction } from '@microsoft/portal-app/lib/redux/createAction';
import { translate } from 'react-i18next';
import { connect, Dispatch } from 'react-redux';
import { AuthzActions, IRootAuthzState } from '../../models';
import { Home } from './Home';
import {
  IConnectedHomeProps,
  IHomeActions,
  IHomeMappedProps
} from './Home.types';

/**
 * Maps the external (i.e. own) props and the state to the properties of the Home control.
 * @param state The current redux state to map to props
 * @param ownProps The external properties to map to props
 * @returns The mapped properties that along with the actions
 * becomes the props (IHomeProps) for the Home control
 */
const mapStateToProps = (
  state: IRootAuthzState,
  _ownProps: IConnectedHomeProps
): IHomeMappedProps => {
  const { items, npdList, currentNpd } = state.app;
  return {
    items,
    npdList,
    currentNpd
  };
};

/**
 * Maps the dispatch actions to the props for the Home control.
 * @param dispatch Dispatches actions to the store
 * @returns The mapped actions that along with the mapped properties
 * becomes the props (IHomeProps) for the Home control
 */
const mapDispatchToProps = (
  dispatch: Dispatch<IRootAuthzState>
): IHomeActions => {
  return {
    getItems: createAction(AuthzActions.getItems, dispatch),
    getNpds: createAction(AuthzActions.getNpdList, dispatch)
  };
};

/**
 * A redux connected Home control.
 * @description Supports translation
 */
export const ConnectedHome = connect(
  mapStateToProps,
  mapDispatchToProps
)(translate('App')(Home));



