import { css, getInitials, getRTL } from '@uifabric/utilities';
import { mergeStyles } from 'office-ui-fabric-react';
import { CommandButton } from 'office-ui-fabric-react/lib/Button';
import { FocusZone } from 'office-ui-fabric-react/lib/FocusZone';
import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { List } from 'office-ui-fabric-react/lib/List';
import {
  IPersonaCoinProps,
  Persona,
  PersonaSize
} from 'office-ui-fabric-react/lib/Persona';
import * as React from 'react';
import { IApplication } from '../../models/aadPAP/application/IApplication';
import { IHomeProps, IHomeState } from './Home.types';

const listPageStyles = require('@microsoft/portal-app/lib/styling/patterns/ListPage.scoped.scss');
const cardStyles = require('@microsoft/portal-app/lib/styling/patterns/Card.scoped.scss');
const homeStyles = require('./Home.scoped.scss');

/**
 * Represents a simple list page with cards
 */
export class Home extends React.Component<IHomeProps, IHomeState> {
  // tslint:disable-next-line:no-any
  private pageFocusRef = React.createRef<HTMLDivElement>();
  public componentDidMount() {
    this._focus();
  }

  public render(): React.ReactNode {
    const { t, npdList } = this.props;
    let list = npdList.applications;

    return (
      <main
        data-automation-id="home-page"
        // ariaLabel="Home page"
        className={css('ms-ListPage', listPageStyles.listPage)}
      >
        <div className={css('ms-ListPageHeader', listPageStyles.pageHeader)}>
          <div
            role="heading"
            aria-labelledby="pageTitle"
            tabIndex={-1}
            aria-level={1}
            ref={this.pageFocusRef}
            className={css('ms-PageTitle', listPageStyles.pageTitle)}
          >
            {t('Home')}
          </div>
        </div>
        <FocusZone>
          <List<IApplication | undefined>
            className={listPageStyles.list}
            items={list}
            onRenderCell={this._onRenderCell}
          />
        </FocusZone>
      </main>
    );
  }

  private _focus = () => {
    const node = this.pageFocusRef.current;
    if (node) {
      node.focus();
    }
  }
  private _onRenderCell = (item: IApplication | undefined): React.ReactNode => {
    if (!item) {
      return null;
    }
    const displayName = item.displayName;
    const description = item.appId;
    return (
      <CommandButton
        ariaLabel={this.props.t(
          "Application card, Application is '{{displayName}}' appId is '{{description}}'",
          {
            displayName,
            description
          }
        )}
        role="listitem"
        className={css(
          cardStyles.actionCard,
          cardStyles.card,
          cardStyles.cardIconHeaderAndDescription,
          cardStyles.cardPrimaryContent,
          cardStyles.cardListItem,
          homeStyles.disablePointer
        )}
        data-is-focusable={true}
        disabled={false}
        styles={{
          rootDisabled: mergeStyles({
            color: 'rgb(0,0,0,0.75)'
          })
        }}
      >
        <div className={css(cardStyles.cardContentIconAndText)}>
          <Persona
            hidePersonaDetails
            text={displayName}
            size={PersonaSize.regular}
            onRenderInitials={this._onRenderInitials}
          />
          <div title={displayName} className={css(cardStyles.cardTitle)}>
            <span className="ms-pii">{displayName}</span>
          </div>
        </div>
        <div
          title={description}
          className={css('ms-TileTwoLineBlock', cardStyles.cardDescription)}
        >
          <span className="ms-pii">{description}</span>
        </div>
      </CommandButton>
    );
  }

  private _onRenderInitials = (
    props: IPersonaCoinProps | undefined
  ): JSX.Element => {
    let { imageInitials } = props!;
    const { allowPhoneInitials, showUnknownPersonaCoin } = props!;
    if (showUnknownPersonaCoin) {
      return <Icon iconName="Help" />;
    }
    const isRTL = getRTL();
    imageInitials =
      imageInitials || getInitials(props!.text, isRTL, allowPhoneInitials);
    if (imageInitials.length > 1) {
      imageInitials = imageInitials[0];
    }
    return imageInitials !== '' ? (
      <span>{imageInitials}</span>
    ) : (
      <Icon iconName="Contact" />
    );
  }
}



